/* Container for the scrolling bar */
.super-badass-bar-container {
  height: auto;
  width: 100vw; /* Full viewport width */
  overflow: hidden; /* Hide overflow for smooth scrolling */
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  position: relative;
}

/* Scrolling content styles */
.super-badass-bar-content {
  display: flex;
  align-items: center;
  font-size: 4rem; /* Adjust the font size as needed */
  font-weight: bold;
  white-space: nowrap; /* Prevent wrapping */
  width: 200%; /* Ensure enough width for scrolling effect */
}

/* Text styles */
.super-badass-bar-text {
  margin: 0 50px;
}

/* PNG image styles */
.super-badass-bar-img {
  width: 8.5rem; /* Adjust the image size as needed */
  height: auto;
  margin: 0 50px;
  display: inline-block;
}

/* Media Queries for Responsiveness */
@media (max-width: 767px) {
  .super-badass-bar-content {
    font-size: 1.2rem; /* Smaller font size for mobile */
  }

  .super-badass-bar-img {
    width: 6rem; /* Smaller image size for mobile */
    height: auto;
    margin: 0 20px; /* Reduce margin for mobile */
  }

  .super-badass-bar-text {
    margin: 0 10px; /* Reduce margin for mobile */
  }
}
